@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  -webkit-user-drag: none; /* 禁用 Safari 和 Chrome 瀏覽器 */
  user-select: none; /* 禁用選取 */
}

:root {
  --primary-color: theme("colors.primary.active");
  --primary-inactive-color: theme("colors.primary.inactive");
  --secondary-color: theme("colors.secondary");
  --success-color: theme("colors.success");
  --accent-color: theme("colors.accent");
  --body-color: theme("colors.body");
  --background-color: theme("colors.background");
  --backgroundTrans-color: theme("colors.backgroundTrans");
  --focusText-color: theme("colors.focusText");
}

.hidden-scrollbar {
  /* 對於 Webkit 瀏覽器（如 Chrome, Safari） */
  ::-webkit-scrollbar {
    display: none;
  }

  /* 對於其他瀏覽器 */
  -ms-overflow-style: none; /* IE 和 Edge */
  scrollbar-width: none; /* Firefox */
}

body {
  touch-action: manipulation;
  -webkit-user-select: none; /* 禁用 Safari 瀏覽器選取 */
  -moz-user-select: none; /* 禁用 Firefox 瀏覽器選取 */
  -ms-user-select: none; /* 禁用 Internet Explorer 瀏覽器選取 */
  user-select: none; /* 禁用所有瀏覽器選取 */
  touch-action: manipulation; /* 禁用觸摸長按和雙擊縮放 */
  -webkit-touch-callout: none; /* 禁用 iOS 上的長按選單 */
}
