/* src/components/Button/index.module.css */
.button {
  padding: 4px 16px;
  border-radius: 4px;
  font-size: 14px;
}

.button-rounded {
  padding: 4px 16px;
  border-radius: 99px;
  font-size: 14px;
}

.button-shorted {
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 16px;
}

.button-full {
  padding: 4px 0px;
  width: 100%;
  border-radius: 4px;
  font-size: 16px;
}

.button-full-flat {
  padding: 4px 0px;
  width: 100%;
  font-size: 16px;
}
.exclusive {
  background-color: rgb(49, 143, 15);
  color: var(--primary-color);
}

.active {
  background-color: var(--secondary-color);
  color: var(--primary-color);
}

.inactive {
  background-color: var(--accent-color);
  color: var(--primary-color);
}

.secondary {
  color: var(--secondary-color);
  border: 1px solid var(--secondary-color);
}

.disabled {
  background-color: #150303;
  color: #a0a0a0;
}

.register {
  z-index: 9999;
  position: absolute;
  right: 1rem;
  background-color: rgb(255 201 14);
  color: rgb(0 0 0);
}

.selected {
  background-color: var(--secondary-color);
  color: var(--primary-color);
  border: 2px solid var(--secondary-color);
}
